        name: '图书1',
        price: '60',
        number: '1',
        sum: '120'
      },
      {
        image:
          'http://demo.hasthemes.com/boighor-preview/boighor/images/product/sm-3/1.jpg',
        name: '图书1',
        price: '60',
        number: '1',
        sum: '120'
      }
    ])
    const deleteRow = (index: any, rows: any) => {
      rows.splice(index, 1)
    }
    return { tableData, deleteRow }
